.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@font-face{
  font-family:"Vipp Neue";src:url(./fonts/VippNeue-Regular.eot)
}

@font-face{
  font-family:"hwkt";src:url(./fonts/STKAITI.TTF)
}

@font-face{
  font-family:"hwzs";src:url(./fonts/STZHONGS.TTF)
}

.hwzs{
  font-family: hwzs,宋体,Serif;
}

.hwkt{
  font-family: hwkt,楷体,Serif;
}

.sm-f{
  font-size: 16px;
}

.md-f{
  font-size: 20px;
}

.lg-f{
  font-size: 30px;
}

/* Large desktop
>1200
*/
@media (min-width: 1200px) {
  .title
  {
    position:absolute;
    top:39%;
    left:100px;
    color:white;
    border-radius:10px;
    background-color:rgba(0,0,0,0);
    padding:20px
  }

}
/* Portrait tablet to landscape and desktop */
@media (min-width: 769px) and (max-width: 1200px) {
  .title
  {
    position:absolute;
    top:100px;
    left:100px;
    color:white;
    border-radius:10px;
    background-color:rgba(0,0,0,0);
    padding:20px
  }


}

/* Landscape phone to portrait tablet
<767
*/
@media (max-width: 767px) {
  .title
  {
    position:relative;
    color:#eee;
    border-radius:10px;
    background-color:rgba(0,0,0,0);
    padding:20px
  }

}



