

.listItem:hover{
    color: #fff;
    background-color: #67E0E3;
}


.listItem{
    color :#345;
    padding: 10px;
    fontSize:20px;
    width: 100%;
    cursor: pointer;
    min-width: 200px;
}


li.ant-list-item{
    padding: 0px;
    margin:0px;
}

div.ant-drawer-body{
    padding: 0px;
    margin:0px;

}

div.ant-drawer-title{
    font-size: 22px;
    font-family: Helvetica;

}

div.ant-drawer-header{
    padding: 20px;
    margin: 0px;
    text-align: center;
}
.ant-statistic-title
{
    font-family: 'arial';

}
.ant-statistic-content-value-int,.ant-statistic-content-value-decimal
{
    font-family: 'Times New Roman';

}
.ant-statistic-content-prefix{
    font-size: 18px;
    color: #61dafb;
}