@font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 400;
    src: url(https://fonts.gstatic.com/s/materialicons/v47/flUhRq6tzZclQEJ-Vdg-IuiaDsNc.woff2) format('woff2');
}

.material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    -webkit-font-feature-settings: 'liga';
    -webkit-font-smoothing: antialiased;
}

.searchBar{
    width:120px;
    transition: width 0.5s;
}

.searchBar:hover{
    width:200px
}
*{
    padding: 0px;
    margin: 0px;
}

.location
{
    position: absolute;
    top: 200px;
    left: 200px;

    background-color: rgba(50,50,255,0.7);
    font-family:Helvetica;
    font-size: 1rem;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border-radius: 5px;
    animation: locationframes  5s infinite;
}

@keyframes locationframes {
    0%{
        font-size: 2rem;

        background-color: darkblue;
    }
    20%{
        font-size: 1rem;
        background-color: blue;
    }
    100%{
        font-size: 1rem;

        background-color: rgba(0,0,0,0.7);
    }
}
